class Shop {
  constructor(id, name, type, warehouse, group) {
    this.id = id                //    1
    this.name = name            //    1
    this.type = type            //   { /  / }
    this.warehouse = warehouse  //  
    this.group = group          // 
  };
  isCurrentReciever = false;    //     
  itemTotalValue = 0;           //   
  itemNewValue = 0;             //     
  itemNotForEmptyValue = 0;     //     
  itemToShipValue = 0;          //    
  currentShipValue = 0;         //  ,  
  priority;                     //  
  createShopListRowElement = function(template, renderResultTable) { //       
    const newShopElement = template.cloneNode(true)
    newShopElement.querySelector('.table-data-shop-name').textContent = this.name
    newShopElement.querySelector('.table-data-item-value').textContent = this.itemTotalValue
    const currentShipValueInput = newShopElement.querySelector('.input-shipping-value')
    currentShipValueInput.addEventListener('change', (evt) => {
      this.changeCurrentShipValue(+currentShipValueInput.value, renderResultTable)
    })
    return newShopElement
  };
  createResultRowElement = function(template, recieverId, itemId) { //       
    const newShopElement = template.cloneNode(true)
    newShopElement.querySelector('.table-data-shop-shipper-name').textContent = this.name
    newShopElement.querySelector('.table-data-shop-shipper-id').textContent = this.id
    newShopElement.querySelector('.table-data-shop-reciever-id').textContent = recieverId
    newShopElement.querySelector('.table-data-shop-item-id').textContent = itemId
    const toShipValue = newShopElement.querySelector('.table-data-shop-shipping-value')
    toShipValue.textContent = this.currentShipValue
    return newShopElement
  };
  changeCurrentShipValue = function(newValue, renderResultTable) { //     
    this.currentShipValue = this.isCurrentReciever ? 0 :
                            (newValue <= this.itemTotalValue) ? newValue :  
                            this.itemTotalValue
    this.changeCurrentShipInputValue()
    if (renderResultTable) renderResultTable()
  };
  changeCurrentShipInputValue = function() { //       currentShipValue
    const currentShopNames = [...document.querySelectorAll('.table-data-shop-name')]
    const currentShopElement = currentShopNames.find(shopName => shopName.textContent === this.name)?.closest('.table-row')
    if (currentShopElement) {
      const currentShopElementInput = currentShopElement?.querySelector('.input-shipping-value')
      currentShopElementInput.value = this.currentShipValue

    }
  };
  createShopRecieverOption = function(template) { //  :    -
    const newOption = template.cloneNode(true)
    newOption.querySelector('.option-shop-reciever').value = this.name
    newOption.querySelector('.option-shop-reciever').textContent = this.name
    return newOption
  };
  makeThisShopReciever = function(isReciever) { //    "-"
    this.isCurrentReciever = isReciever
  };
  fillTotalValue = function(currentShopsArray) { //   
    const currentShop = currentShopsArray.find(shop => shop[1] === this.name)
    this.itemTotalValue = currentShop === undefined ? 0 : currentShop[2]
  };
  fillItemSpecificValues = function() { //          
    if (this.itemTotalValue > 0) {
      this.itemNotForEmptyValue = this.type === '' ? this.itemTotalValue - 1 : Math.floor(this.itemTotalValue / 2)
      this.itemNewValue = this.type === '' || this.type === '' ? this.itemTotalValue : this.itemTotalValue - 1
    }
  };
  calculatePriority = function(prioritySettings) { //  -  
    let shopPriorityValue = this.name in prioritySettings && this.type !== '' ? prioritySettings[this.name] : 
                            this.type === '' ? 0 : 
                            1
    this.priority = (prioritySettings[this.group] + prioritySettings[this.warehouse] + shopPriorityValue)
  };
}

